export default {
  name: "Home",
  components: { GithubIcon, LinkedinIcon, TwitterIcon, FacebookIcon, MailIcon },
  data() {
    return {
      avatar: require("@/assets/avatar-1.webp"),
    };
  },
  head: {
    title: "Lydia Rodriguez - Zumba Trainer",
    meta: [
